body{
  margin:0px;
}
.title{
  text-align: center;
  font-size: 30px;
  font-weight: 600;
  margin: 50px 0px;
}

.underline{
  border-bottom: 2px solid gray;
  margin: 30px 200px;
}

.container{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 0px 200px;
}

.itembox1{
  display: flex;
  position: relative;
}
.item1_2{
  position:absolute;
  top: 10%;
  margin:0px 40px;
  display: flex;
  background-color: rgba(0, 0, 0, 0.28);
  color: white;
}
.item1_2_box{
  padding-left: 20px;
}
.item1_2_title{
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 20px;
}

.itembox2{
  padding-left: 100px;
}
/* ================================card css start*/
.border{
  border: 1px solid rgb(99, 99, 228);
  margin: 50px 0px;
}
.c_items1{
  display: flex;
  justify-content: center;
  margin-bottom: 80px;
}
.c_item1_2{
  display: flex;
  flex-wrap: wrap;
  flex:10;
  overflow: hidden;
}
.c_item1_1,.c_item1_3{
  margin:auto;
  flex:1;
}
.cardimg img{
  width: 100px;
  height: 100px;
}
.cardimg:hover{
  border: 2px solid blue;
}
/* .cardimg:focus{
  border: 2px solid blue;
} */
.cardimg::after{
  border: 2px solid blue;
}



.c_items2{
  border: 1px solid rgb(195, 195, 195);
  padding: 20px 20px 0px 20px;
  width: 600px;
  height: 475px;
}
.c_item2_title{
  text-align: center;
  font-size: 1.5em;
  margin-bottom: 50px;
}

.c_item2_text{
  font-size: 1em;
}

.c_item2_img{
  text-align: center;
  overflow: hidden;
  margin-bottom: 50px;
}