.sub4_4{
  margin:0px 150px;
}

.title{
  text-align: center;
  font-size: 30px;
  font-weight: 600;
  margin: 50px 0px;
}
.underline{
  border-bottom: 2px solid gray;
  margin: 30px 0px;
}

.icons{
  margin: 20px;
  display: flex;
  justify-content:baseline;
  flex-wrap: wrap;
}
.iconbox{
  flex-basis: 18%;
  border: 1px solid rgb(183, 180, 180);
  width: 150px;
  height: 80px;
  margin: 10px;
}
.iconbox img{
  display: block;
  width: 70%;
  height: 70%;
  object-fit:contain;
  margin: auto;
}
.subtitle{
  font-size: 1.4rem;
  font-weight: 600;
}
