.footer{
  width: 100%;
  height: 100%;
  background-color: rgb(69, 69, 69);
  font-weight: bolder;
  color: rgb(211, 211, 211);
  font-size: 14px;
}
.footer-container{
  display: flex;
  flex-direction: column;
  padding:0px 50px;
}
.footer-link{
  border-bottom: 1px solid rgb(211, 211, 211);
  display: flex;
  flex-direction: row;
  padding: 20px 0px;
}
.footer-link li{
  list-style: none;
}
.footer-link li:first-child{
  padding-right: 50px;
}
.footer-link{
  display: flex;
}
.footer-contants{
  display: flex;
}
.contant-item{
  text-align: left;
}
.footer-contant{
  padding-top:30px;
  flex: 1;
}

.footer-contant2{
  text-align: left;
  padding-bottom:20px;
}



