*{
  padding:0px
}
.link{
  color: white;
  text-decoration-line: none;
}
.hidden{
  display: none;
}
.Nav{
  background-color: rgb(29, 44, 77);
}

.NavList_container{
  display: flex;
  flex-direction:row;
  align-items: flex-start;
  justify-content: center;
}
.logoImg{
  flex:2;
  padding-top:12.5px;
}

.item_container{
  display: flex;
  flex-direction: column;
  flex:1;
  color: white;
  font-weight: bolder;
  padding:35px 43px;
  font-size: 20px;
  position: relative;
}

.dropdown{
  display: block;
  padding: 16px 0px;
  font-size: 16px;
  border: none;
  position: absolute;
  top:100%;
  z-index: 1;
}

.item_container:hover{
  color:rgb(109, 132, 228);
}


.item{
  list-style: none;
  padding:10px 0px;
}




