*{
  margin: 0px;
}

.main_img{
  position: relative;
}

.slide_buttons{
  cursor: pointer;
  position:absolute;
  top: 90%;
  left: 45%;
}
.button{
  margin:0px 5px;
}
.contant1{
  display: flex;
  flex-direction: column;
  padding-top: 70px;
}
.contant1_title{
  font-size: 40px;
  font-weight: bolder;
  margin-bottom: 20px;
}
.contant1_sub{
  font-size: 20px;
}
.contant1_text{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 50px;
}
.contant1_imgs{
  display: flex;
  justify-content: space-around;
  margin: 0px 500px;
}
.contant1_item{
  position:relative;
  
}
.img_title{
  position:absolute;
  top: 5%;
  left: 5%;
  color: white;
  font-size: 20px;
  border-bottom: 2px solid white;
}
.img_sub{
  position:absolute;
  top: 15%;
  left: 5%;
  width: 50%;
  color: white;
  font-size: 20px;
}
.imgtext{
  position: absolute;
  top: 20%;
  left: 20%;
  color: white;
}

.imgtext .title{
  font-size: 35px;
  font-weight: 800;
  margin-bottom: 40px;
}
.imgtext .subtitle{
  font-size: 20px;
  font-weight: 500;
}
/* ======================================컨텐츠 1 끝 */
/* ======================================컨텐츠 2 시작 */
.contant2{
  display: flex;
  flex-direction: column;
  background-color: rgb(29, 43, 76);
  margin-top: -280px;
  padding-top:350px;
  padding-bottom: 100px;
}
.contant2_text{
  color: white;
  text-align: center;
  padding-bottom: 50px;
}
.contant2_imgs{
  display: flex;
  justify-content: space-around;
  margin: 0px 250px;
}

.contant2_item{
  background-color: white
}
.img2_title{
  padding: 40px 0px 10px 0px;
  color: black;
  text-align: center;
  font: 25px bolder;
}
.img2_sub{
  color:black;
  text-align: center;
  padding: 10px 0px 40px 0px;
  font-size: 17px;
}
/* ======================================컨텐츠 2 끝 */
/* ======================================컨텐츠 3 시작 */
.contant3{
  padding: 100px 0px
}
.contant3_items{
  display: flex;
  justify-content: space-around;
}
.contant3_item{
  display: flex;
  padding:30px 15px;
  border: 1px rgb(202, 197, 197) solid;
}

.contant3_text .item_title{
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 15px;
}
.contant3_text .item_sub{
  color:rgb(104, 104, 104);
}

.contant3_img .item_img{
  padding-left: 70px;
}

.slideImg{
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.img{
  background-size: cover;
  opacity: 0.4;
  scale:1
}

.fade_in{
  height: 100%;
  background-size: cover;
  opacity: 1;
  scale:1.1;
  transition: all 3s;
}
