.contentContainer{
  margin:100px 150px;
}
.title{
  text-align: center;
  font-size: 30px;
  font-weight: 600;
  margin: 50px 0px;
}
.underline{
  border-bottom: 2px solid gray;
  margin: 30px 150px;
}
.btnList{
  text-align: center;
  margin-bottom: 100px;
}
.btnList button{
  background-color: white;
  width: 300px;
  height: 70px;
  margin: 0px 30px;
  border-radius: 50px;
  border: 1px solid gray;
  font-size: 1rem;
  font-weight: 900;
}
.btnList button:hover{
  background-color: rgb(63, 85, 146);
  color: white;
}
.btnList button:focus{
  background-color: rgb(63, 85, 146);
  color: white;
}

.content1{
  text-align: center;
}
.content1_text{
  margin: 50px 0px;
}
.imgs{
  display: flex;
  justify-content: space-around;
  margin: 100px 0px;
}

.content2{
  margin-bottom: 50px;
  margin: auto;
}
.content2_container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.container_content2{
  display: flex;
  border: 1px solid gray;
  width: 30%;
  margin: 10px 10px;
  align-items: center;
}
.content2_text{
  flex-basis: 80%;
  text-align: left;
  margin: 40px 20px;
}
.content2_text p:first-child{
  margin-bottom: 15px;
}
.content2_img{
  margin: 20px 20px;
}
